import Formulario from './InteresForm'

function IndexInteres() {
  return (
    <div className="container mt-4">
        <div className="row">
          <div className='col-12 col-md-6 bg-light shadow p-3 mb-5 bg-body rounded'>
            <h2 className='text-center'>Formulario inters compuesto</h2>
              <Formulario />
          </div>
          <div className='col-12 col-md-5 offset-md-1'>
            <div className='row bg-light shadow p-3 mb-5 bg-body rounded'>
              <h2>Qu es el inters compuesto?</h2>
              <div className='col-12'>
                <div className='col'>
                <p className='lead'>El inters compuesto es calcular cuanto dinero vamos a ganar en el transcurso de los aos
                    si hacemos un depsito inicial y luego seguimos contribuyendo dinero
                    anualmente.
                </p>
                <p className='lead'>
                    1.depsito incial: dposito que se realiza la primera vez.<br/>
                    2.depsito anual: dinero que se ir introduciendo anualmente.<br/>
                    3.aos: aos durante los que se harn los depsitos anuales.<br/>
                    4.inters estimado: es la tasa de inters que se aplicar al depsito 
                    inicial.
                </p>
                </div>
              </div>
              <div className='col-12'>
                <h6>Frmula</h6>
                <p className='lead'>Capital final = CO x (1+Ti)^t
                <br/>
                CO es la capital inicial, Ti la tasa del inters anual y t es el tiempo
                que dura la inversin
                </p>
              </div>
            </div>
          </div>
        </div>
    </div>
  )
}

export default IndexInteres
